import React, { useMemo } from "react";
import { useStyles } from "./styles";
import { getPage3 } from "../../../utils/Data";
import { Typography } from "@material-ui/core";
import { Footer } from "../../Footer";
import { BANNER_TITLE_3 } from "../../../utils/constants";
import { Banner } from "../../Banner";
import image from "./banner3.png";
import Divider from "@material-ui/core/Divider";

export const Page3 = () => {
  const classes = useStyles();
  const data: any = useMemo(() => getPage3(), []);
  const keys: string[] = useMemo(() => Object.keys(data), [data]);
  return (
    <div className={classes.root}>
      <Banner title={BANNER_TITLE_3} image={image} />
      {keys.map((key: string) => (
        <div className={classes.section1} key={key}>
          {data[key].map((item: any, index: number) => (
            <div key={index}>
              {item.title && <div className={classes.title}>{item.title}</div>}
              <div className={classes.title2}>{item.title2}</div>
              <Typography
                variant="overline"
                style={{ color: "grey" }}
                display="block"
                gutterBottom
              >
                {item.subtitle}
              </Typography>
              <iframe
                style={{ width: "100%", height: 600, border: "none" }}
                src={item.src}
                title={item.title2}
              ></iframe>
            </div>
          ))}
          <Divider style={{ marginTop: 20 }} />
        </div>
      ))}
      <Footer />
    </div>
  );
};
