import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import { useStyles } from "./styles";
import { EcxelIcon } from "../ExcelIcon";

function ListItemLink(props: any) {
  return <ListItem component="a" {...props} />;
}

type Item = {
  text: string;
  link?: string;
};

type Props = {
  links: Item[];
};

export const Links: React.FC<Props> = ({ links }) => {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <List component="nav" aria-label="main mailbox folders">
        {links.map((item: Item, index: number) => (
          <ListItem key={index} style={{ borderBottom: "1px solid grey" }}>
            <ListItemIcon>
              <EcxelIcon viewBox="0 0 34 34" />
            </ListItemIcon>
            <ListItemLink href={"./files/" + item.link} target={"_blank"}>
              <div className={classes.text}>{item.text}</div>
            </ListItemLink>
          </ListItem>
        ))}
      </List>
    </div>
  );
};
